// Utility classes
// --------------------------------------------------
.dtbl{
  display: table;
  table-layout:fixed;
  width: 100%;
  .dtbl-col{
    display: table-cell;
    vertical-align: middle;
  }
}


// border
.border-left{
  border-left: @border-base;
}
.border-right{
  border-right: @border-base;
}
.border-top{
  border-top: @border-base;
}
.border-bottom{
  border-bottom: @border-base;
}
.border-none{
  border: none;
}


